.footer{
  border-top: 1px solid #E0E0E0;
  padding: .44em 0 1.87em;
  &__inner{
    display: flex;
  }
  &__left{
    margin-right: 3.875em;
    flex-shrink: 0;
  }
  &__logo{
    width: 15.5em;
    display: block;
    img{
      width: 100%;
      display: block;
    }
  }
  &__c{
    font-weight: 300;
    font-size: .75em;
    line-height: 142%;
    margin-top: 1.33em;
  }
  &__links{
    width: 31.25em;
    max-width: 100%;
    column-count: 2;
    column-gap: 1.25em;
    margin-right: 6.13em;
    padding-top: 1.2em;
  }
  &__link{
    min-width: 6em;
    display: block;
    font-size: .875em;
    line-height: 142%;
    &:not(:last-child){
      margin-bottom: 1em;
    }
  }
  &__pay{
    margin-left: auto;
    padding-top: 1.44em;
    &-title{
      font-size: .875em;
      line-height: 142%;
    }
    &-items{
      display: flex;
      align-items: center;
      margin-top: .3125em;
    }
    &-item{
      display: flex;
      align-items: center;
      &:not(:last-child){
        margin-right: 1.0625em;
      }
    }
  }
  &__right{
    margin-left: auto;
    padding-top: 1.62em;
  }
  &__btn{
    height: 2.5em;
    width: 9.1875em;
  }
  &__socials{
    display: flex;
    align-items: center;
    justify-content: flex-end;
    margin-top: 1.25em;
  }
  &__social{
    &:hover{
      img{
        transform: translateY(-.625em);
      }
    }
    img{
      transition: all .3s;
    }
    &:not(:last-child){
      margin-right: .625em;
    }
  }
}


@media screen and (max-width: 992px){
  .footer{
    &__inner{
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 1.875em .625em;
      padding-top: 1em;
    }
    &__links{
      margin: 0;
      grid-row: 1/3;
      grid-column: 2/3;
      width: 100%;
    }
    &__left{
      margin: 0;
    }
    &__right{
      margin: 0;
      grid-column: 1/3;
      display: flex;
      flex-direction: column;
      align-items: center;
    }
    &__socials{
      justify-content: center;
    }
  }
}


@media screen and (max-width: 768px){
  .footer{
    padding: .6em 0 .87em;
    &__inner{
      position: relative;
    }
    &__logo{
      position: absolute;
      top: 0;
      left: 0;
    }
    &__links{
      column-count: 1;
      padding-top: 0;
    }
    &__link{
      min-width: unset;
    }
    &__pay{
      padding-top: 0;
      &-items{
        flex-wrap: wrap;
      }
      &-item{
        margin-bottom: .25em;
      }
    }
    &__c{
      margin: 0;
    }
    &__right{
      padding-top: .625em;
    }
  }
}
