.main,
.info{
  &__lines{
    position: absolute;
    svg{
      width: 100%;
      height: auto;
    }
    ._view2{
      display: none;
    }
  }
}

.main{
  padding: 2.87em 5.3125em 5.25em 18.75em;
  color: #fff;
  position: relative;
  overflow: hidden;
  &__logo{
    width: 25.3125em;
    max-width: 100%;
    margin-top: -2.87em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__inner{
    margin-top: 9.375em;
    display: grid;
    grid-template-columns: calc(100% - 56.75em) 49.75em;
    gap: 7em;
  }
  &__content{

  }
  &__img{
    margin-top: -16.5em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__title{
    font-size: 3.75em;
    line-height: 100%;
    font-weight: 700;
  }
  &__text{
    margin-top: 1.625em;
    p{
      font-size: 2.375em;
      line-height: 136%;
    }
    a{
      text-decoration: underline;
    }
    b{
      font-weight: 700;
    }
    a{
      text-decoration: underline;
    }
  }
  &__lines{
    left: .5em;
    width: 117.25em;
    top: 0;
    opacity: 0;
  }
  &._active{
    .main__lines{
      opacity: 1;
    }
  }
}

._line{
  transition: all 1.4s;
}

.info{
  position: relative;
  overflow: hidden;
  padding: 4.83em 11.81em 20em 11.81em;
  display: grid;
  grid-template-columns: 39.5em calc(100% - 45.5em);
  justify-content: space-between;
  &__content{
    max-width: 42.3em;
  }
  &__stat-title,
  &__text p,
  &__title{
    font-size: 1.875em;
    line-height: 142%;
  }
  &__title{
    font-weight: 500;
  }
  &__text{
    margin-top: 2em;
    font-weight: 300;
  }
  &__stat{
    margin-top: 2em;
    transform: translateX(11.4em);
    &-title{
      font-weight: 300;
    }
    &-num{
      color: #E51D3E;
      font-size: 3.4375em;
      line-height: 107%;
      font-weight: 700;
    }
  }
  &__lines{
    bottom: 0;
    left: 0;
    width: 100%;
  }
  &__line2,
  &__line3{
    display: none;
  }
}

.info-slider{
  position: relative;
  padding-right: 3.75em;
  .swiper{
    &-container{
      position: relative;
      z-index: 2;
    }
    &-slide{
      opacity: 0;
      &-active{
        opacity: 1;
      }
    }
  }
  &__bg{
    position: absolute;
    top: 3.63em;
    right: 0;
    width: 34.1875em;
  }
}

.stat{
  padding: 10em 11.75em 10em 18.625em;
  display: grid;
  grid-template-columns: calc(100% - 46.375em - 4.625em) 46.375em;
  justify-content: space-between;
  gap: 2em;
  &__lines{
    pointer-events: none;
    position: absolute;
    width: 100%;
    height: auto;
    left: 0;
    top: -4em;
  }
  &__col2{
    padding-top: 6em;
  }
  &__img{
    width: 100%;
    display: block;
  }
  &__num{
    font-size: 14.5em;
    line-height: 112%;
    font-weight: 700;
    color: $primary;
  }
  &__text{
    margin-top: .4em;
    font-size: 2.8125em;
    line-height: 134%;
    font-weight: 500;
    &-count{
      text-decoration: underline;
      text-decoration-skip-ink: none;
    }
    &-count1{
      color: #e58221;
    }
    &-count2{
      color: $primary;
    }
  }
  &__nav{
    display: flex;
    margin-top: 4em;
    margin-bottom: 1.875em;
  }
  &__btn{
    padding: 0 7.5em;
    height: 5.375em;
    border-radius: 3.125em;
    text-transform: unset;
    span{
      font-size: 2.1875em;
    }
  }
  &__desc{
    font-size: 1.75em;
    line-height: 129%;
    font-weight: 300;
  }
}

.stat-block{
  padding-left: 2.125em;
  position: relative;
  &__title{
    position: absolute;
    left: 0;
    span{
      writing-mode: tb-rl;
      font-size: 1.125em;
      line-height: 129%;
      transform: rotate(-180deg);
    }
  }
  &__title1{
    top: 0;
    color: #f37c00;
  }
  &__title2{
    bottom: 0;
    color: $primary;
  }
  &__items{
    display: grid;
    grid-template-columns: repeat(10,calc(10% - 3.375em / 10));
    gap: 0 .375em;
  }
  &__item{
    padding-bottom: 95.38%;
    @include bgImgSize(100%);
    &._cup{
      background-image: url(../img/cup.svg);
    }
    &._cup-fill{
      background-image: url(../img/cup-fill.svg);
    }
    &._heart{
      background-image: url(../img/heart.png);
    }
    &._heart-fill{
      background-image: url(../img/heart-fill.svg);
    }
  }
}


@media screen and (max-width: 992px){
  .main{
    padding: 2em 1.8em 7.31em;
    &__inner{
      grid-template-columns: 100%;
      gap: 4.16em;
      margin-top: 3.24em;
    }
    &__content{
      padding: 0;
      order: 2;
    }
    &__logo{
      width: 15.9935em;
      margin: 0 auto;
    }
    &__title{
      font-size: 2.5em;
    }
    &__text{
      margin-top: 1.1em;
      p{
        font-size: 1.375em;
      }
    }
    &__img{
      margin: 0;
    }
    &__lines{
      top: -1em;
      width: 100%;
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }

  .info{
    padding: 3.88em 1.75em 2.69em;
    grid-template-columns: 100%;
    gap: 3.44em;
    &__stat-title,
    &__text p,
    &__title{
      font-size: 1.375em;
    }
    &__stat{
      margin-top: 1em;
      transform: unset;
      padding-left: 10.86em;
      &-title{
        font-weight: 300;
      }
      &-num{
        font-size: 2.5em;
      }
    }
    &__text{
      margin-top: 1em;
    }
  }

  .info-slider{
    .swiper{
      &-container{
        overflow: unset;
      }
    }
  }

  .stat{
    padding: 2em 1em;
    grid-template-columns: 100%;
    &__col{
      font-size: .75em;
    }
    &__col2{
      padding: 0;
    }
  }
}


@media screen and (max-width: 576px){
  .info{
    overflow: unset;
    padding-left: 0;
    padding-right: 0;
    &__lines{
      display: none;
    }
    &__line2{
      position: absolute;
      left: 0;
      top: -5em;
      display: block;
      width: 27.125em;
      height: auto;
    }
    &__content{
      overflow: hidden;
      padding: 0 1.75em;
      position: relative;
    }
    &__line3{
      position: absolute;
      top: 8.23em;
      left: -2.68em;
      width: 33.05831em;
      display: block;
    }
  }

  .info-slider{
    padding-right: 4.15em;
    padding-left: 1.75em;
    overflow: hidden;
    &__bg{
      width: 22.7em;
      top: 2.32em;
    }
  }

  .stat{
    &__col{
      font-size: .625em;
    }
  }
}
