.fg {
  position: relative;
  margin: 0;
  label{
    display: block;
    font-size: 1.125em;
    line-height: 129%;
    font-weight: 300;
    color: #fff;
    margin-bottom: .625em;
  }
  input {
    border: none;
    padding: 0 1.25em;
    height: 4em;
    width: 100%;
    font-size: 1em;
    background: rgba(255, 255, 255, 0.45);
    color: #000;
  }
}

.m-check {
  position: relative;
  input {
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
    &:checked+label {
      &::before {
        background-size: .75em auto;
      }
    }
    &:disabled+label {
      color: #AAAAAA;
      pointer-events: none;
    }
  }
  label {
    color: #fff;
    font-weight: 300;
    display: block;
    padding-left: 2em;
    position: relative;
    min-height: 1.125em;
    font-size: 1em;
    line-height: 129%;
    cursor: pointer;
    &:before {
      content: '';
      position: absolute;
      z-index: 1;
      background-color: #f7c4a4;
      background-image: url(../img/check.svg);
      @include bgImgSize(0%);
      width: 1.125em;
      height: 1.125em;
      left: 0;
      top: 0;
      transition: all .100s;
    }
  }
  a{
    text-decoration: underline;
    position: relative;
    z-index: 2;
    &:hover{
      color: #fff;
    }
  }
}
