.about{
  padding: 4em 8em 4.4375em 18.75em;
  display: grid;
  grid-template-columns: calc(100% - 50.5em) 41.5em;
  gap: 0 9em;
  position: relative;
  background: linear-gradient(51deg, #f3a45b 3.24%, #fdeec2 100%);
  &__line,
  &__line2{
    position: absolute;
    top: 4em;
    left: 0;
    width: 100%;
    height: auto;
  }
  &__line2{
    display: none;
  }
  &__img{
    position: relative;
    z-index: 2;
    img{
      width: 100%;
      display: block;
    }
    ._view2{
      display: none;
    }
  }
  &__content{
    padding-top: 6.75em;
    max-width: 34.5em;
  }
  &__title{
    text-transform: uppercase;
    font-size: 3.45em;
    font-weight: 700;
  }
  &__text{
    margin-top: 2.06em;
  }
  &__text,
  &__text2{
    font-weight: 300;
    p{
      font-size: 1.5em;
      line-height: 142%;
      @include lastM(0 0 2em);
    }
  }
  &__text2{
    margin-top: 1.25em;
    text-align: right;
  }
  &__bottom{
    grid-column: 1/-1;
    display: flex;
    justify-content: flex-end;
    padding-right: 28em;
  }
  &__logo{
    max-width: 100%;
    width: 44.1875em;
  }
  &__line3{
    position: absolute;
    right: 0;
    bottom: 10.375em;
    width: 39em;
    height: auto;
  }
}

.help{
  padding: 11.375em 26.875em 0 25.3125em;
  position: relative;
  &__title{
    text-transform: uppercase;
    font-size: 2.375em;
    color: $primary;
    text-align: center;
  }
  &__desc{
    margin-top: 1em;
    font-size:  1.25em;
    line-height: 142%;
    text-align: center;
    font-weight: 300;
  }
  &__top{
    display: grid;
    grid-template-columns: 39.6875em 23em;
    justify-content: space-between;
    position: relative;
    z-index: 2;
  }
  &__col2{
    padding-top: 1.6875em;
  }
}

.help-group{
  padding: 3.5em 4em 1.5em;
  background: linear-gradient(144deg, #f3a45b 0%, #e53552 100%);
  border-radius: 1.5em;
  @include grid100gap(2.125em);
}

.help-group2{
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  &__title{
    font-size: 2.125em;
    line-height: 129%;
  }
  &__field{
    margin-top: 1.875em;
    width: 100%;
    input{
      width: 100%;
      border: none;
      text-align: center;
      font-size: 2em;
      padding: 0 1em;
      background: #f0f0f0;
      height: 3em;
    }
  }
  &__btn{
    margin-top: 3.125em;
    width: 100%;
    height: 5.375em;
    border-radius: 3.125em;
    text-transform: unset;
    span{
      font-size: 2.125em;
    }
  }
}

.help2{
  padding: 14em 2em 1.375em;
}


@media screen and (max-width: 992px){
  .about{
    padding: 2em 1.75em 3.2em;
    grid-template-columns: 100%;
    gap: 2.5em;
    &__content{
      padding: 0;
    }
    &__img{
      order: -1;
    }
    &__title{
      font-size: 2.5em;
    }
    &__text,
    &__text2{
      p{
        font-size: 1.25em;
      }
    }
    &__text{
      margin-top: 2.5em;
    }
    &__bottom{
      padding: 0;
    }
    &__line,
    &__line3{
      display: none;
    }
  }

  .help{
    padding: 2em 1em;
    &__top{
      grid-template-columns: 100%;
      gap: 1em;
      margin-bottom: 3em;
    }
    .stat__lines{
      display: none;
    }
  }
}


@media screen and (max-width: 576px){
  .about{
    &__line{
      display: none;
    }
    &__img{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }

  .help{
    padding-top: 0;
    &__title{
      font-size: 1.75em;
    }
    &__desc{
      font-size: 1em;
    }
  }

  .help-group{
    padding: 1.25em 1em;
    gap: 1em;
    width: calc(100% + 2em);
    margin-left: -1em;
    border-radius: 0;
  }

  .help2{
    padding: 1em;
  }
}
